import axios from "axios";
import { createStore } from "vuex";

const store = createStore({
    state:{
        url: 'https://api.github.com/users/',
        user: null,
        repos: null,
        currentSort: 'name'
    },
    
    mutations:{
        getUser(state, payload){
            state.user = payload
        },
        clear(state){
            state.user = null
            state.repos = null
        },
        getRepos(state, payload){
            state.repos = payload
        },
        sortRepos(state, payload){
            state.currentSort = payload
        }
    },
    
    actions:{
        async getUser({commit, state}, search) {
            try {
                const response = await axios.get(`${state.url}${search}`)
                const repos = await axios.get(`${state.url}${search}/repos`)
                commit('getRepos', repos.data)
                commit('getUser', response.data)
            } catch (error) {
                console.log('Произошла ошибка при прлучении пользователя');
            }
        }
    },
    
    getters:{
        reposSort(state){
            if (state.repos != null) {
                return state.repos.sort((prev, next) => {
                    if (prev[state.currentSort] > next[state.currentSort]) {
                        return 1
                    }
                    if (prev[state.currentSort] <= next[state.currentSort]) {
                        return -1
                    }
                })
            }
        }
    }
})

export default store