<template>
    <div class="user" v-if="user">
        <div class="user__left">
            <img :src="user.avatar_url" alt="">
            <a :href="user.html_url">Посетить</a>
        </div>
        <div class="user__right">
            <p>{{ user.login }}</p>
            <p>Репозиториев: <span>{{user.public_repos}}</span></p>
            <p>Создан: <span>{{new Date(user.updated_at).toLocaleDateString()}}</span></p>
            <p>Подписщиков: <span>{{user.followers}}</span></p>
            <p>Подписок: <span>{{user.following}}</span></p>
        </div>
    </div>
    
    <div class="sort" v-if="user">
        <h2 class="sort__title">Сортировка</h2>
        <div class="sort__btns">
            <button class="sort__btns-btn"
             v-for="(btn, i) in buttons"
            :key="btn.name"
            :class="{active: i == isActive}"
            @click="qwe(i, btn.descr)">
            {{ btn.name }}
            </button>
        </div>
    </div>
    
    <h2 class="title" v-else>Пользователя с таким никнеймом нет</h2>
</template>

<script>
import { mapState, mapMutations } from "vuex";

    export default {
        data() {
            return {
                buttons: [
                    {name: 'Имя', descr: 'name'},
                    {name: 'Звезды', descr: 'stargazers_count'},
                    {name: 'Дата', descr: 'created_at'}
                ],
                isActive: 0,
            }
        },
        computed:{
            ...mapState(['user'])
        },
        methods: {
            ...mapMutations(['sortRepos']),
            
            qwe(i, descr){
                this.isActive = i
                this.sortRepos(descr)
            }
        },
    }
</script>