<template>
    <div class="search">
        <div class="container">
            <div class="search__block">
                <input type="text" placeholder="Введите имя пользователя" v-model="search" @keydown.enter="searchUser">
                <span @click="delInp" @keydown.delete="delInp">❌</span>
            </div>
            <button class="search__btn" @click="searchUser">НАЙТИ</button>
        </div>
    </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";

    export default {
        data() {
            return {
                search: ''
            }
        },
        methods:{
            ...mapActions(['getUser']),
            ...mapMutations(['clear']),
            
            searchUser(){
                this.getUser(this.search)
                this.search = ''
            },
            delInp(){ 
                this.search = ''
                this.clear()
            }
        },
    }
</script>
